import React from 'react';
import styled, { css } from 'styled-components';
import {
  typography,
  space,
  color,
  layout,
  border,
  borderRadius,
  position,
} from 'styled-system';
import themeGet from '@styled-system/theme-get';
import tag from 'clean-tag';

import Box from './Box';
import theme from './ThemeProvider/theme';

import { customColor } from './utils/getColor';
import blacklist from './utils/blacklist';

const active = css`
  ${customColor('hoverColor')};
  ${customColor('hoverBg', 'backgroundColor')};
  ${customColor('hoverBorder', 'borderColor')};
  font-weight: bold;
`;

export const buttonStyle = css`
  padding: 0;
  border: none;
  font-family: inherit;
  line-height: 1;
  text-decoration: none;
  ${typography}
  ${layout}
  ${position}
  ${space}
  ${color}
  ${border}
  ${borderRadius}
  appearance: none;
  transition: all ${themeGet('duration', 250)}ms;
  cursor: pointer;
  &:hover,
  &:focus {
    ${props => !props.disabled && active}
    outline: none;
  }
  ${props => props.active && !props.disabled && active}
  ${props =>
    props.disabled &&
    `
    border-color: ${theme.colors.gray}
    color: ${theme.colors.gray}
    cursor: not-allowed;
    opacity: 0.5;
  `}
`;

const ButtonBase = styled(tag)`
  ${buttonStyle};
`;

const InButtonSpan = props => <Box is="span" {...props} />;

const Button = ({
  leftIcon,
  rightIcon,
  iconSpacing,
  children,
  verticalAlign,
  ...props
}) => (
  <ButtonBase {...props}>
    {leftIcon ? (
      <Box is={leftIcon} mr={iconSpacing} verticalAlign={verticalAlign} />
    ) : null}
    <InButtonSpan verticalAlign={verticalAlign}>{children}</InButtonSpan>
    {rightIcon ? (
      <Box is={rightIcon} ml={iconSpacing} verticalAlign={verticalAlign} />
    ) : null}
  </ButtonBase>
);

Button.defaultProps = {
  blacklist,
  is: 'button',
  border: '2px solid',
  borderColor: 'lightOrange',
  bg: 'white',
  color: 'text',
  hoverColor: 'white',
  hoverBg: 'primaryHover',
  hoverBorder: 'primaryHover',
  px: '2.5em',
  py: '0.75em',
  iconSpacing: '0.25em',
  borderRadius: '1.5em',
  display: 'inline-block',
  textAlign: 'center',
};

Button.displayName = 'Button';

Button.danger = props => (
  <Button
    bg="danger"
    borderColor="danger"
    hoverBg="dangerHover"
    hoverBorder="dangerHover"
    {...props}
  />
);

Button.choice = props => (
  <Button
    color="colorOrange"
    borderRadius="1em"
    type="button"
    px="1em"
    width="100%"
    {...props}
  />
);

Button.open = props => (
  <Button
    bg="titleBlue"
    border="2px solid"
    borderColor="titleBlue"
    color="white"
    px="4.25em"
    borderRadius="2em"
    {...props}
  />
);

Button.tabs = props => (
  <Button
    bg="lightPurple"
    color="white"
    border="2px solid"
    borderColor="lightPurple"
    hoverColor="titleBlue"
    hoverBg="white"
    hoverBorder="white"
    borderRadius="1em 1em 0 0"
    {...props}
  />
);

Button.circle = props => (
  <Button
    hoverColor="white"
    borderRadius="50%"
    px="0"
    py="0"
    {...props}
  />
);

Button.download = props => (
  <Button
    color="lightOrange"
    hoverColor="white"
    borderRadius="50%"
    px="0"
    py="0"
    {...props}
  />
);

Button.secondary = props => (
  <Button
    bg="secondary"
    borderColor="secondary"
    hoverBg="secondaryHover"
    hoverBorder="secondaryHover"
    {...props}
  />
);

Button.outline = props => (
  <Button
    border="2px solid"
    borderColor="primary"
    bg="transparent"
    color="primary"
    hoverColor="white"
    {...props}
  />
);

Button.outline.danger = props => (
  <Button.danger
    border="2px solid"
    borderColor="danger"
    bg="transparent"
    color="danger"
    hoverColor="white"
    {...props}
  />
);

Button.menu = props => (
  <Button
    border="1px solid transparent"
    borderColor="transparent"
    bg="transparent"
    color="text"
    px="6em"
    borderRadius="0"
    {...props}
  />
);

Button.transparent = props => (
  <Button
    border="1px solid transparent"
    borderColor="transparent"
    bg="transparent"
    color="titleBlue"
    hoverColor="titleBlue"
    hoverBg="transparent"
    hoverBorder="transparent"
    {...props}
  />
);

export default Button;
