import React, { PureComponent } from 'react';

import Box from './Box'
import VerticalCenter from './VerticalCenter'
import theme from './ThemeProvider/theme'

class FullpageVerticalCenter extends PureComponent {
  state = {
    isCenter: true
  }

  handleCenter = (isCenter) => this.setState({ isCenter })

  render() {
    const { noHeader, noFooter, ...props } = this.props
    const { isCenter } = this.state
    return (
      <Box
        height={isCenter && '100vh'}
        minHeight="100vh"
        mt={!noHeader && `-${theme.headerHeight}`}
        pt={!noHeader && theme.headerHeight}
        mb={!noFooter && `-${theme.footerHeight}`}
        pb={!noFooter && theme.footerHeight}
      >
        <VerticalCenter onAlignChange={this.handleCenter} {...props} />
      </Box>
    );
  }
}

export default FullpageVerticalCenter;
