import React from 'react'

import { IoIosMenu } from 'react-icons/io';
import withResponsive from 'hoc/withResponsive';

import Box from '../components/Box';
import Flex from '../components/Flex';
import Button from '../components/Button';
import Text from '../components/Text';
import Link from '../components/Link';

const Header = ({ siteTitle, currentState, menuClick, setState, isMobile, bg, ...props }) => (
  <Flex
    position="fixed"
    bg={isMobile ? bg : 'transparent'}
    top={0}
    left={0}
    right={0}
    alignItems="center"
    zOrder={2}
    {...props}
  >
    <Box px="1em" flex={1}>
      <Flex justifyContent="space-between" alignItems="center" mx="1em">
        <Button.transparent px="0" py="0" color="titleBlue" onClick={menuClick}>
          <IoIosMenu size="2em" />
        </Button.transparent>
        <Link to="/" color="titleBlue">
          <Text.serif>{siteTitle}</Text.serif>
        </Link>
      </Flex>
    </Box>
    {Boolean(currentState) && (
      <Box position="fixed"
        top="0"
        bottom="0"
        left="0"
        right="0"
        bg="rgba(0,0,0,0.7)"
        onClick={() => setState(0)}
        cursor="unset"
      />
    )}
  </Flex>
)

export default withResponsive(Header)
