import React from 'react';

import SVG from './SVG'

const index = (props) => {
  return (
    <SVG viewBox="0 0 29.333 29.333" {...props}>
      <g fill="none" stroke="currentColor" strokeWidth="5">
        <circle cx="14.667" cy="14.667" r="14.667" stroke="none"/>
        <circle cx="14.667" cy="14.667" r="12.167" fill="none"/>
      </g>
    </SVG>
  );
};

export default index;
