import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import { withSize } from 'react-sizeme'
import isFunction from 'lodash/isFunction'

import Box from './Box';

class VerticalCenter extends PureComponent {
  static getDerivedStateFromProps({ size }, { boxRef, count }) {
    if (boxRef && size.height && count < 6) {
      const boxContainerHeight = boxRef.getBoundingClientRect().height
      if (boxContainerHeight && size.height) {
        const shouldCenter = size.height > boxContainerHeight;
        return {
          shouldCenter,
          count: count + 1,
        }
      }
    }
    return {}
  }

  state = {
    count: 0,
    shouldCenter: true,
  }

  componentDidUpdate({ size: height }, prevState) {
    const { shouldCenter } = this.state
    if (shouldCenter !== prevState.shouldCenter) {
      this.props.onAlignChange(shouldCenter)
    }
    if (height !== this.props.size.height && !this.timer) {
      this.timer = setTimeout(this.clearCount, 500)

    }
  }

  clearCount = () => {
    this.setState({ count: 0 })
    delete this.timer
  }

  handleBoxRef = (boxRef) => {
    this.setState({ boxRef })
  }

  render() {
    const {
      children,
      onAlignChange,
      size,
      ...props
    } = this.props;
    const { shouldCenter } = this.state;
    return (
      <Box
        position="relative"
        width="100%"
        height="100%"
        {...props}
      >
        <Box
          position={shouldCenter && 'absolute'}
          top={shouldCenter ? '50%' : 0}
          width={1}
          transform={shouldCenter && 'translateY(-50%)'}
          ref={this.handleBoxRef}
        >
          {isFunction(children) ? children(shouldCenter) : children}
        </Box>
      </Box>
    );
  }
}

VerticalCenter.displayName = 'VerticalCenter';

VerticalCenter.propTypes = {
  onAlignChange: PropTypes.func,
}

VerticalCenter.defaultProps = {
  onAlignChange: () => {}
}

export default withSize({ monitorHeight: true, monitorWidth: false })(VerticalCenter);
